import React, { useState, useEffect } from "react";
import Header from "./Header";
import Footer from "./Footer";
import teamUserImg from "../images/card/3.jpg";
import { useParams, useLocation } from "react-router-dom";
import axios from "axios";
import BpChart from "./BpChart";
import WeightChart from "./WeightChart";

function Details(props) {
  console.log(props);
  let location = useLocation();
  const userParams = useParams();
  console.log(location.state.userImage);

  const [user, setUser] = useState({});
  useEffect(() => {
    fetchUsers(userParams);
  }, [userParams]);

  async function fetchUsers(params) {
    try {
      const { data, status } = await axios.get(
        `http://54.251.26.118/api/get-user?userId=${params.userId}`
      );
      if (status === 200) {
        console.log(data);
        setUser(data?.data[0]);
        return data;
      }
      setUser([]);
      return Promise.reject({ data });
    } catch (error) {
      setUser([]);
      return Promise.reject({ error: "server_error" });
    }
  }

  return (
    <>
      <Header />
      <main className="mdl-layout__content mx-layout-tb mx-layout-tm-menu px-3 mb-5">
        <div className="container-fluid">
          <div className="row">
            <div className="col-12 mt-4">
              <div className="health-card-details pt-3 pb-5">
                <div className="float-left pl-2">
                  <img
                    className="health-profile-details-picture"
                    src={location?.state?.userImage}
                    alt="Card User"
                    style={{ background: "#fff" }}
                  />
                </div>
                <div className="float-left card-content">
                  <h5 className="">{user.name}</h5>
                  <p className="card-text">{user.age}</p>
                </div>
                <div className="float-right health-links mb-3">
                  <a href="javascript:;" title="Weekly" className="mr-4">
                    Weekly
                  </a>
                  <a href="javascript:;" title="Monthly" className="mr-4">
                    Monthly{" "}
                  </a>
                  <a href="javascript:;" title="Yearly" className="mr-5">
                    Yearly
                  </a>
                </div>
                <div className="clearfix"></div>
                <div className="col-12">
                  <div className="row">
                    <div className="col-6">
                      <BpChart userId={user.userId} />
                    </div>
                    <div className="col-6">
                      <WeightChart userId={user.userId} />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </main>
      <Footer />
    </>
  );
}

export default Details;
