import React from "react";
import teamUserImg1 from "../images/card/1.jpeg";
import teamUserImg2 from "../images/card/2.jpeg";
import teamUserImg3 from "../images/card/2.jpg";
import teamUserImg4 from "../images/card/3.jpg";
import teamUserImg5 from "../images/card/4.jpg";
import teamUserImg6 from "../images/card/5.jpg";
import teamUserImg7 from "../images/card/6.jpeg";
import teamUserImg8 from "../images/card/7.jpg";
import userIcon from "../images/user_icon.png";
import heartIcon from "../../src/images/heart.svg";
import weightIcon from "../../src/images/weight.svg";
import { Link } from "react-router-dom";

function Card(props) {
  const maleImagesArray = [
    teamUserImg1,
    teamUserImg4,
    teamUserImg5,
    teamUserImg7,
    teamUserImg8,
  ];
  const femaleImagesArray = [teamUserImg2, teamUserImg3, teamUserImg6];
  const randomMaleImage =
    maleImagesArray[(Math.random() * maleImagesArray.length) | 0];
  const randomFemaleImage =
    femaleImagesArray[(Math.random() * femaleImagesArray.length) | 0];
  const { user } = props;
  const userNamesArray = [
    "Oliver",
    "Jake",
    "Noah",
    "James",
    "Jack",
    "Connor",
    "Liam",
  ];
  const femaleUserArray = ["varsha", "Gunjan", "Swathi"];
  const getImage = (userName) => {
    if (userNamesArray.includes(userName.split(" ")[0])) {
      return randomMaleImage;
    } else if (femaleUserArray.includes(userName.split(" ")[0])) {
      return randomFemaleImage;
    }
    return userIcon;
  };
  const userImage = getImage(user.name);

  return (
    <Link to={`/details/${user.userId}`} state={{ userImage: userImage }}>
      <div className="health-card">
        <div className="float-left">
          <img
            className="health-profile-picture"
            style={{ background: "#fff" }}
            src={userImage}
            alt=""
          />
        </div>
        <div className="float-left card-content card-small dark-grey">
          <h5 className="">{user.name}</h5>
          <p className="card-text">{user.age}</p>
        </div>
        <div className="clearfix"></div>

        <div className="health-card-body">
          <div className="yellow health-icon health-icon-img-w">
            <img src={heartIcon} alt="" />
          </div>
          <div className="green health-icon health-icon-img-w">
            <img src={weightIcon} alt="" />
          </div>
        </div>
      </div>
    </Link>
  );
}

export default Card;
