import React, { useState, useEffect } from "react";
import heartIcon from "../../src/images/heart.svg";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
  PointElement,
  LineController,
  LineElement,
} from "chart.js";
import { Bar } from "react-chartjs-2";
import moment from "moment";
import axios from "axios";

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
  PointElement,
  LineController,
  LineElement
);

function BpChart(props) {
  const [bpData, setBpData] = useState([]);
  useEffect(() => {
    if (props.userId) {
      fetchBpData(props.userId);
    }
  }, [props.userId]);

  async function fetchBpData(userId) {
    try {
      const { data, status } = await axios.get(
        `http://54.251.26.118/api/get-user-bp?userId=${userId}`
      );
      if (status === 200) {
        console.log(data.data);
        setBpData(data.data);
        return data;
      }
      setBpData([]);
      return Promise.reject({ data });
    } catch (error) {
      setBpData([]);
      return Promise.reject({ error: "server_error" });
    }
  }

  const bpDataFormat = bpData?.map((bp) => {
    return {
      high: bp.systolic_high,
      low: bp.diastolic_low,
      pulse: bp.pulse,
      date: moment(bp.createdDate).format("MM/DD/YYYY"),
    };
  });
  const datesArray = [];
  const bpLowValues = [];
  const bpHighValues = [];
  const pulseArray = [];
  bpDataFormat?.forEach((bp) => {
    datesArray.push(moment(bp.date, "DD/MM/YYYY").format("DD/MM"));
    bpLowValues.push(bp.low);
    bpHighValues.push(bp.high);
    pulseArray.push(bp.pulse);
  });
  console.log(bpDataFormat);
  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: "top",
      },
      title: {
        display: true,
        text: "Weekly Blood Pressure Chart",
      },
    },
  };

  const chartData = {
    labels: datesArray,
    datasets: [
      {
        label: "Pulse",
        data: pulseArray,
        backgroundColor: "#bb0900",
        borderColor: "#FF6965",
        type: "line",
        order: 0,
      },
      {
        label: "High",
        data: bpHighValues,
        backgroundColor: "rgba(255, 99, 132, 0.5)",
      },
      {
        label: "Low",
        data: bpLowValues,
        backgroundColor: "rgba(53, 162, 235, 0.5)",
      },
    ],
  };
  return (
    <div className="border p-3 rounded">
      <div className="health-icon-font mb-3">
        <img src={heartIcon} />
        <span className="health-icon-font pl-1">Blood Pressure</span>
      </div>
      <div id="chartContainer3" style={{ height: "100%", width: "100%" }}>
        <Bar data={chartData} options={options} />
      </div>
      <table className="table mt-5">
        <tbody>
          {bpDataFormat?.length > 0 &&
            bpDataFormat?.map((bp, i) => (
              <tr key={i}>
                <td>
                  <i className="time-stamp">{bp.date}</i>
                </td>
                <td>
                  {bp.high}/{bp.low}
                </td>
                <td>Pulse : {bp.pulse}</td>
              </tr>
            ))}
        </tbody>
      </table>
    </div>
  );
}

export default BpChart;
