import React, { useState, useEffect } from "react";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import { Bar } from "react-chartjs-2";
import weightIcon from "../../src/images/weight.svg";
import moment from "moment";
import axios from "axios";

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

function WeightChart(props) {
  const [weightData, setWeightData] = useState([]);
  useEffect(() => {
    if (props.userId) {
      fetchWeightData(props.userId);
    }
  }, [props.userId]);
  async function fetchWeightData(userId) {
    try {
      const { data, status } = await axios.get(
        `http://54.251.26.118/api/get-user-weight?userId=${userId}`
      );
      if (status === 200) {
        console.log(data.data);
        setWeightData(data.data);
        return data;
      }
      setWeightData([]);
      return Promise.reject({ data });
    } catch (error) {
      setWeightData([]);
      return Promise.reject({ error: "server_error" });
    }
  }

  const weightDataFormat = weightData?.map((weight) => {
    return {
      date: moment(weight.createdDate).format("MM/DD/YYYY"),
      weightValue: weight.weightValue,
    };
  });

  const datesArray = [];
  const weightValues = [];
  weightDataFormat?.forEach((weight) => {
    datesArray.push(moment(weight.date, "MM/DD/YYYY").format("MM/DD"));
    weightValues.push(weight.weightValue);
  });

  const chartData = {
    labels: datesArray,
    datasets: [
      {
        label: "In lbs",
        data: weightValues,
        backgroundColor: ["#77dd77"],
      },
    ],
  };
  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: "top",
      },
      title: {
        display: true,
        text: "Weekly Weight Chart",
      },
    },
  };
  return (
    <div className="bg-white mb-3">
      <div className="border p-3 rounded">
        <div className="mb-3">
          <img src={weightIcon} alt="" />{" "}
          <span className="health-icon-font pl-1">Weight</span>
        </div>
        <div id="chartContainer3" style={{ height: "100%", width: "100%" }}>
          <Bar data={chartData} options={options} />
        </div>
        <table className="table mt-5">
          <tbody>
            {weightDataFormat?.length > 0 &&
              weightDataFormat?.map((weight, i) => (
                <tr key={i}>
                  <td>
                    <i className="time-stamp">{weight.date}</i>
                  </td>
                  <td>{weight.weightValue} lbs</td>
                </tr>
              ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default WeightChart;
