import React, { Component } from "react";
import { Link } from "react-router-dom";

class Header extends Component {
  render() {
    return (
      <header className="mdl-layout__header">
        <div className="mdl-layout__header-row mdl-color--white">
          <span className="mdl-layout-title purple-lite welnessapp-logo ml-2">
            WELLNESS APP
          </span>
          <div className="mdl-layout-spacer"></div>
          <nav className="mdl-navigation mdl-layout--large-screen-only">
            <a href="#" className="mx-logout ml-2 mdl-color--white">
              <div className="float-right mx-profile-pic ">
                <img
                  src="public/images/user.png"
                  className="rounded-circle"
                  id="mx-logout"
                  alt="USERPIC"
                />
                <i className="material-icons mdl-color-text--black">
                  arrow_drop_down
                </i>
              </div>
              <div className="float-right pr-3 ">
                <h5 className="text-right mx-header-username">Jason Bourne</h5>
                <p className="text-right mx-header-userid mdl-color-text--grey-600">
                  ID 854587458
                </p>
              </div>
            </a>
            <ul
              className="mdl-menu mdl-menu--bottom-right mdl-js-menu mdl-js-ripple-effect py-0"
              htmlFor="mx-logout"
            >
              <li className="mdl-menu__item">
                <i className="material-icons mr-2">exit_to_app</i>{" "}
                <a href="index.html">Logout</a>
              </li>
            </ul>
          </nav>
        </div>
        <div className="mx-layout-tm-menu">
          <div className="mx-layout-left-drawer-tm mx-sidebar-menu ">
            <ul className="mx-sidenavmenu-items">
              <li>
                <Link to={`/dashboard`} className="mx-left-drawer-menu active">
                  <i className="material-icons ">home</i>
                  <span className="menu-label">Home</span>
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </header>
    );
  }
}

export default Header;
